"use client";

import { useState } from "react";
import { AudioDevice, RouteConfig } from "@/app/page";
import { Button } from "@/components/ui/button";

interface ControlPanelProps {
  routeConfig: RouteConfig;
  devices: AudioDevice[];
  isRouting: boolean;
  onRoutingStateChange: (isRouting: boolean, message: string) => void;
  onLogUpdate: (message: string) => void;
}

export function ControlPanel({
  routeConfig,
  devices,
  isRouting,
  onRoutingStateChange,
  onLogUpdate,
}: ControlPanelProps) {
  const [isProcessing, setIsProcessing] = useState(false);

  const getDeviceName = (deviceId: string) => {
    const device = devices.find(d => d.id === deviceId);
    return device?.name || "Unknown Device";
  };

  const handleStartRouting = async () => {
    try {
      setIsProcessing(true);

      // Validate configuration
      if (!routeConfig.sourceId || !routeConfig.destinationId) {
        const errorMsg = "Error: Please configure source and destination devices first";
        onRoutingStateChange(false, errorMsg);
        onLogUpdate(errorMsg);
        return;
      }

      if (routeConfig.sourceId === routeConfig.destinationId) {
        const errorMsg = "Error: Source and destination cannot be the same device";
        onRoutingStateChange(false, errorMsg);
        onLogUpdate(errorMsg);
        return;
      }

      const sourceDevice = devices.find(d => d.id === routeConfig.sourceId);
      const destDevice = devices.find(d => d.id === routeConfig.destinationId);

      if (!sourceDevice || !destDevice) {
        const errorMsg = "Error: Selected devices are not available";
        onRoutingStateChange(false, errorMsg);
        onLogUpdate(errorMsg);
        return;
      }

      if (sourceDevice.status !== "active") {
        const errorMsg = `Error: Source device "${sourceDevice.name}" is not active`;
        onRoutingStateChange(false, errorMsg);
        onLogUpdate(errorMsg);
        return;
      }

      // Simulate starting the routing process
      await new Promise(resolve => setTimeout(resolve, 1000));

      const successMsg = `Audio routing started: ${getDeviceName(routeConfig.sourceId)} → ${getDeviceName(routeConfig.destinationId)}`;
      onRoutingStateChange(true, successMsg);
      onLogUpdate(successMsg);
    } catch (error) {
      const errorMsg = "Error: Failed to start audio routing";
      onRoutingStateChange(false, errorMsg);
      onLogUpdate(errorMsg);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleStopRouting = async () => {
    try {
      setIsProcessing(true);

      // Simulate stopping the routing process
      await new Promise(resolve => setTimeout(resolve, 500));

      const stopMsg = "Audio routing stopped";
      onRoutingStateChange(false, stopMsg);
      onLogUpdate(stopMsg);
    } catch (error) {
      const errorMsg = "Error: Failed to stop audio routing";
      onRoutingStateChange(false, errorMsg);
      onLogUpdate(errorMsg);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleReset = async () => {
    try {
      setIsProcessing(true);

      // Simulate reset process
      await new Promise(resolve => setTimeout(resolve, 300));

      const resetMsg = "System reset - ready for new configuration";
      onRoutingStateChange(false, resetMsg);
      onLogUpdate(resetMsg);
    } catch (error) {
      const errorMsg = "Error: Failed to reset system";
      onRoutingStateChange(false, errorMsg);
      onLogUpdate(errorMsg);
    } finally {
      setIsProcessing(false);
    }
  };

  const canStartRouting = !isRouting && routeConfig.sourceId && routeConfig.destinationId && !isProcessing;
  const canStopRouting = isRouting && !isProcessing;

  return (
    <div className="space-y-6">
      {/* Current Configuration Display */}
      {routeConfig.sourceId && routeConfig.destinationId && (
        <div className="p-4 rounded-lg bg-muted/50 border">
          <div className="text-sm font-medium mb-2">Current Configuration:</div>
          <div className="space-y-1">
            <div className="flex items-center space-x-2 text-sm">
              <span className="text-muted-foreground">Source:</span>
              <span className="font-medium">{getDeviceName(routeConfig.sourceId)}</span>
            </div>
            <div className="flex items-center space-x-2 text-sm">
              <span className="text-muted-foreground">Destination:</span>
              <span className="font-medium">{getDeviceName(routeConfig.destinationId)}</span>
            </div>
          </div>
        </div>
      )}

      {/* Control Buttons */}
      <div className="grid grid-cols-1 gap-3">
        <Button
          onClick={handleStartRouting}
          disabled={!canStartRouting}
          className="h-12 text-base font-medium"
          size="lg"
        >
          {isProcessing && !isRouting ? "Starting..." : "Start Routing"}
        </Button>

        <Button
          onClick={handleStopRouting}
          disabled={!canStopRouting}
          variant="destructive"
          className="h-12 text-base font-medium"
          size="lg"
        >
          {isProcessing && isRouting ? "Stopping..." : "Stop Routing"}
        </Button>

        <Button
          onClick={handleReset}
          disabled={isProcessing}
          variant="outline"
          className="h-10"
        >
          {isProcessing ? "Resetting..." : "Reset System"}
        </Button>
      </div>

      {/* System Information */}
      <div className="space-y-3 pt-4 border-t">
        <div className="text-sm font-medium">System Information</div>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <div className="text-muted-foreground">Active Devices</div>
            <div className="font-medium">
              {devices.filter(d => d.status === "active").length} / {devices.length}
            </div>
          </div>
          <div>
            <div className="text-muted-foreground">Virtual Devices</div>
            <div className="font-medium">
              {devices.filter(d => d.type === "virtual").length}
            </div>
          </div>
        </div>
        
        <div className="text-xs text-muted-foreground">
          Driver Version: 2.1.0 • Zero Latency Mode: Enabled
        </div>
      </div>

      {/* Quick Actions */}
      {!routeConfig.sourceId || !routeConfig.destinationId ? (
        <div className="p-3 rounded-lg bg-yellow-50 dark:bg-yellow-950 border border-yellow-200 dark:border-yellow-800">
          <div className="text-sm text-yellow-800 dark:text-yellow-200">
            Configure source and destination devices to enable routing controls
          </div>
        </div>
      ) : null}
    </div>
  );
}
