"use client";

import { AudioDevice } from "@/app/page";

interface AudioDeviceListProps {
  devices: AudioDevice[];
}

export function AudioDeviceList({ devices }: AudioDeviceListProps) {
  const getDeviceTypeColor = (type: AudioDevice["type"]) => {
    switch (type) {
      case "input":
        return "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200";
      case "output":
        return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200";
      case "virtual":
        return "bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200";
      default:
        return "bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200";
    }
  };

  const getStatusColor = (status: AudioDevice["status"]) => {
    return status === "active"
      ? "bg-green-500"
      : "bg-gray-300 dark:bg-gray-600";
  };

  if (devices.length === 0) {
    return (
      <div className="text-center py-8 text-muted-foreground">
        No audio devices available
      </div>
    );
  }

  return (
    <div className="space-y-3">
      {devices.map((device) => (
        <div
          key={device.id}
          className="flex items-center justify-between p-3 rounded-lg border bg-card hover:bg-accent/50 transition-colors"
        >
          <div className="flex items-center space-x-3">
            <div
              className={`w-3 h-3 rounded-full ${getStatusColor(device.status)}`}
              title={device.status === "active" ? "Active" : "Inactive"}
            />
            <div>
              <div className="font-medium">{device.name}</div>
              <div className="text-sm text-muted-foreground">
                ID: {device.id}
              </div>
            </div>
          </div>
          <div className="flex items-center space-x-2">
            <span
              className={`px-2 py-1 text-xs rounded-full font-medium ${getDeviceTypeColor(
                device.type
              )}`}
            >
              {device.type.toUpperCase()}
            </span>
          </div>
        </div>
      ))}
    </div>
  );
}
