"use client";

import { useState } from "react";
import { AudioDevice, RouteConfig } from "@/app/page";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Label } from "@/components/ui/label";

interface AudioRouteConfiguratorProps {
  devices: AudioDevice[];
  config: RouteConfig;
  onConfigChange: (config: RouteConfig) => void;
}

export function AudioRouteConfigurator({
  devices,
  config,
  onConfigChange,
}: AudioRouteConfiguratorProps) {
  const [localConfig, setLocalConfig] = useState<RouteConfig>(config);
  const [error, setError] = useState<string>("");

  const handleSourceChange = (value: string) => {
    setLocalConfig(prev => ({ ...prev, sourceId: value }));
    setError("");
  };

  const handleDestinationChange = (value: string) => {
    setLocalConfig(prev => ({ ...prev, destinationId: value }));
    setError("");
  };

  const handleApplyConfig = () => {
    try {
      if (!localConfig.sourceId || !localConfig.destinationId) {
        setError("Please select both source and destination devices");
        return;
      }

      if (localConfig.sourceId === localConfig.destinationId) {
        setError("Source and destination cannot be the same device");
        return;
      }

      const sourceDevice = devices.find(d => d.id === localConfig.sourceId);
      const destDevice = devices.find(d => d.id === localConfig.destinationId);

      if (!sourceDevice || !destDevice) {
        setError("Selected devices are not available");
        return;
      }

      onConfigChange(localConfig);
      setError("");
    } catch (err) {
      setError("Failed to apply configuration");
    }
  };

  const handleReset = () => {
    const resetConfig = { sourceId: "", destinationId: "" };
    setLocalConfig(resetConfig);
    onConfigChange(resetConfig);
    setError("");
  };

  const getDeviceDisplayName = (device: AudioDevice) => {
    return `${device.name} (${device.type.toUpperCase()})`;
  };

  const activeDevices = devices.filter(device => device.status === "active");

  return (
    <div className="space-y-6">
      {/* Source Selection */}
      <div className="space-y-2">
        <Label htmlFor="source-select">Source Device</Label>
        <Select value={localConfig.sourceId} onValueChange={handleSourceChange}>
          <SelectTrigger id="source-select">
            <SelectValue placeholder="Select source device" />
          </SelectTrigger>
          <SelectContent>
            {activeDevices.map((device) => (
              <SelectItem key={device.id} value={device.id}>
                {getDeviceDisplayName(device)}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      {/* Destination Selection */}
      <div className="space-y-2">
        <Label htmlFor="destination-select">Destination Device</Label>
        <Select value={localConfig.destinationId} onValueChange={handleDestinationChange}>
          <SelectTrigger id="destination-select">
            <SelectValue placeholder="Select destination device" />
          </SelectTrigger>
          <SelectContent>
            {devices.map((device) => (
              <SelectItem key={device.id} value={device.id}>
                {getDeviceDisplayName(device)}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      {/* Route Preview */}
      {localConfig.sourceId && localConfig.destinationId && (
        <div className="p-3 rounded-lg bg-muted/50 border">
          <div className="text-sm font-medium mb-2">Route Preview:</div>
          <div className="flex items-center space-x-2 text-sm">
            <span className="font-medium">
              {devices.find(d => d.id === localConfig.sourceId)?.name}
            </span>
            <span className="text-muted-foreground">→</span>
            <span className="font-medium">
              {devices.find(d => d.id === localConfig.destinationId)?.name}
            </span>
          </div>
        </div>
      )}

      {/* Error Message */}
      {error && (
        <div className="p-3 rounded-lg bg-destructive/10 border border-destructive/20">
          <div className="text-sm text-destructive">{error}</div>
        </div>
      )}

      {/* Action Buttons */}
      <div className="flex space-x-3">
        <Button 
          onClick={handleApplyConfig}
          disabled={!localConfig.sourceId || !localConfig.destinationId}
          className="flex-1"
        >
          Apply Configuration
        </Button>
        <Button 
          variant="outline" 
          onClick={handleReset}
          disabled={!localConfig.sourceId && !localConfig.destinationId}
        >
          Reset
        </Button>
      </div>
    </div>
  );
}
