"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { AudioDeviceList } from "@/components/AudioDeviceList";
import { AudioRouteConfigurator } from "@/components/AudioRouteConfigurator";
import { AudioMonitor } from "@/components/AudioMonitor";
import { ControlPanel } from "@/components/ControlPanel";
import { AudioLog } from "@/components/AudioLog";

export interface AudioDevice {
  id: string;
  name: string;
  type: "input" | "output" | "virtual";
  status: "active" | "inactive";
}

export interface RouteConfig {
  sourceId: string;
  destinationId: string;
}

export default function Home() {
  const [devices] = useState<AudioDevice[]>([
    { id: "1", name: "Spotify", type: "output", status: "active" },
    { id: "2", name: "Discord", type: "input", status: "active" },
    { id: "3", name: "OBS Studio", type: "input", status: "active" },
    { id: "4", name: "Virtual Audio Device 1", type: "virtual", status: "inactive" },
    { id: "5", name: "Virtual Audio Device 2", type: "virtual", status: "inactive" },
    { id: "6", name: "System Audio", type: "output", status: "active" },
  ]);

  const [routeConfig, setRouteConfig] = useState<RouteConfig>({
    sourceId: "",
    destinationId: "",
  });

  const [isRouting, setIsRouting] = useState(false);
  const [statusMessage, setStatusMessage] = useState("Ready to configure audio routing");

  const [logs, setLogs] = useState<string[]>([]);

  const appendLog = (message: string) => {
    const timestamp = new Date().toLocaleTimeString();
    setLogs((prevLogs) => [...prevLogs, `[${timestamp}] ${message}`]);
  };

  const handleRouteConfigChange = (config: RouteConfig) => {
    setRouteConfig(config);
    setStatusMessage("Route configuration updated");
    appendLog(`Route configuration updated: source ${config.sourceId} → destination ${config.destinationId}`);
  };

  const handleRoutingStateChange = (routing: boolean, message: string) => {
    setIsRouting(routing);
    setStatusMessage(message);
    appendLog(message);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-background to-muted/20 p-6">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header */}
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold tracking-tight">Virtual Audio Loopback Driver</h1>
          <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
            Route audio between applications with zero additional latency. 
            Configure virtual audio devices and monitor real-time audio flow.
          </p>
        </div>

        {/* Main Content Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Left Column */}
          <div className="space-y-6">
            {/* Device Manager */}
            <Card>
              <CardHeader>
                <CardTitle>Audio Devices</CardTitle>
                <CardDescription>
                  Available audio devices and applications
                </CardDescription>
              </CardHeader>
              <CardContent>
                <AudioDeviceList devices={devices} />
              </CardContent>
            </Card>

            {/* Route Configuration */}
            <Card>
              <CardHeader>
                <CardTitle>Route Configuration</CardTitle>
                <CardDescription>
                  Select source and destination for audio routing
                </CardDescription>
              </CardHeader>
              <CardContent>
                <AudioRouteConfigurator
                  devices={devices}
                  config={routeConfig}
                  onConfigChange={handleRouteConfigChange}
                />
              </CardContent>
            </Card>
          </div>

          {/* Right Column */}
          <div className="space-y-6">
            {/* Control Panel */}
            <Card>
              <CardHeader>
                <CardTitle>Control Panel</CardTitle>
                <CardDescription>
                  Start, stop, and manage audio routing
                </CardDescription>
              </CardHeader>
              <CardContent>
                <ControlPanel
                  routeConfig={routeConfig}
                  devices={devices}
                  isRouting={isRouting}
                  onRoutingStateChange={handleRoutingStateChange}
                  onLogUpdate={appendLog}
                />
              </CardContent>
            </Card>

            {/* Audio Monitor */}
            <Card>
              <CardHeader>
                <CardTitle>Audio Monitor</CardTitle>
                <CardDescription>
                  Real-time audio level visualization
                </CardDescription>
              </CardHeader>
              <CardContent>
                <AudioMonitor isRouting={isRouting} routeConfig={routeConfig} devices={devices} />
              </CardContent>
            </Card>

            {/* Status Panel */}
            <Card>
              <CardHeader>
                <CardTitle>System Status</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex items-center justify-between">
                    <span className="text-sm font-medium">Status:</span>
                    <span className={`text-sm px-2 py-1 rounded-full ${
                      isRouting 
                        ? "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200" 
                        : "bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200"
                    }`}>
                      {isRouting ? "Routing Active" : "Idle"}
                    </span>
                  </div>
                  <div className="text-sm text-muted-foreground">
                    {statusMessage}
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Routing History Log */}
            <Card>
              <CardHeader>
                <CardTitle>Routing History</CardTitle>
                <CardDescription>
                  Log of routing events and system messages
                </CardDescription>
              </CardHeader>
              <CardContent>
                <AudioLog logs={logs} />
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
