"use client";

import { useState, useEffect } from "react";
import { AudioDevice, RouteConfig } from "@/app/page";

interface AudioMonitorProps {
  isRouting: boolean;
  routeConfig: RouteConfig;
  devices: AudioDevice[];
}

interface AudioLevel {
  deviceId: string;
  level: number;
  peak: number;
}

export function AudioMonitor({ isRouting, routeConfig, devices }: AudioMonitorProps) {
  const [audioLevels, setAudioLevels] = useState<AudioLevel[]>([]);

  useEffect(() => {
    let interval: NodeJS.Timeout;

    if (isRouting && routeConfig.sourceId && routeConfig.destinationId) {
      // Simulate real-time audio levels
      interval = setInterval(() => {
        try {
          setAudioLevels([
            {
              deviceId: routeConfig.sourceId,
              level: Math.random() * 100,
              peak: Math.random() * 100,
            },
            {
              deviceId: routeConfig.destinationId,
              level: Math.random() * 80, // Slightly lower for destination
              peak: Math.random() * 80,
            },
          ]);
        } catch (error) {
          console.error("Error updating audio levels:", error);
        }
      }, 100); // Update every 100ms for smooth animation
    } else {
      setAudioLevels([]);
    }

    return () => {
      if (interval) {
        clearInterval(interval);
      }
    };
  }, [isRouting, routeConfig.sourceId, routeConfig.destinationId]);

  const getDeviceName = (deviceId: string) => {
    const device = devices.find(d => d.id === deviceId);
    return device?.name || "Unknown Device";
  };

  const getLevelColor = (level: number) => {
    if (level > 80) return "bg-red-500";
    if (level > 60) return "bg-yellow-500";
    return "bg-green-500";
  };

  if (!isRouting || !routeConfig.sourceId || !routeConfig.destinationId) {
    return (
      <div className="text-center py-8 text-muted-foreground">
        <div className="mb-2">Audio monitoring inactive</div>
        <div className="text-sm">Start routing to see real-time audio levels</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Route Flow Indicator */}
      <div className="flex items-center justify-center space-x-4 p-4 rounded-lg bg-muted/50">
        <div className="text-sm font-medium text-center">
          <div>{getDeviceName(routeConfig.sourceId)}</div>
          <div className="text-xs text-muted-foreground">SOURCE</div>
        </div>
        <div className="flex items-center space-x-1">
          <div className="w-2 h-2 rounded-full bg-primary animate-pulse"></div>
          <div className="w-4 h-0.5 bg-primary"></div>
          <div className="w-2 h-2 rounded-full bg-primary animate-pulse"></div>
          <div className="w-4 h-0.5 bg-primary"></div>
          <div className="w-2 h-2 rounded-full bg-primary animate-pulse"></div>
        </div>
        <div className="text-sm font-medium text-center">
          <div>{getDeviceName(routeConfig.destinationId)}</div>
          <div className="text-xs text-muted-foreground">DESTINATION</div>
        </div>
      </div>

      {/* Audio Level Meters */}
      <div className="space-y-4">
        {audioLevels.map((levelData) => (
          <div key={levelData.deviceId} className="space-y-2">
            <div className="flex items-center justify-between">
              <span className="text-sm font-medium">
                {getDeviceName(levelData.deviceId)}
              </span>
              <span className="text-xs text-muted-foreground">
                {Math.round(levelData.level)}%
              </span>
            </div>
            
            {/* Level Bar */}
            <div className="relative h-4 bg-muted rounded-full overflow-hidden">
              <div
                className={`h-full transition-all duration-100 ease-out ${getLevelColor(levelData.level)}`}
                style={{ width: `${Math.max(0, Math.min(100, levelData.level))}%` }}
              />
              
              {/* Peak Indicator */}
              <div
                className="absolute top-0 w-0.5 h-full bg-white opacity-80 transition-all duration-100"
                style={{ left: `${Math.max(0, Math.min(100, levelData.peak))}%` }}
              />
            </div>

            {/* Level Status */}
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>0%</span>
              <span className={`font-medium ${
                levelData.level > 80 ? "text-red-500" : 
                levelData.level > 60 ? "text-yellow-500" : 
                "text-green-500"
              }`}>
                {levelData.level > 80 ? "HIGH" : 
                 levelData.level > 60 ? "MEDIUM" : 
                 "NORMAL"}
              </span>
              <span>100%</span>
            </div>
          </div>
        ))}
      </div>

      {/* Latency Indicator */}
      <div className="p-3 rounded-lg bg-green-50 dark:bg-green-950 border border-green-200 dark:border-green-800">
        <div className="flex items-center justify-between">
          <span className="text-sm font-medium text-green-800 dark:text-green-200">
            Latency Status
          </span>
          <span className="text-sm font-bold text-green-600 dark:text-green-400">
            0ms - Zero Latency
          </span>
        </div>
        <div className="text-xs text-green-600 dark:text-green-400 mt-1">
          Virtual loopback active with no additional delay
        </div>
      </div>
    </div>
  );
}
