"use client";

interface AudioLogProps {
  logs: string[];
}

export function AudioLog({ logs }: AudioLogProps) {
  return (
    <div className="h-64 overflow-y-auto rounded-lg border bg-card p-4 text-sm font-mono text-muted-foreground">
      {logs.length === 0 ? (
        <div className="text-center py-8">No routing history available</div>
      ) : (
        logs.map((log, index) => (
          <div key={index} className="mb-1 last:mb-0 whitespace-pre-wrap">
            {log}
          </div>
        ))
      )}
    </div>
  );
}
